import React, { Component } from "react";
import PropTypes from "prop-types";
import AntTable from "antd/lib/table";
import "antd/lib/table/style/index.css";
import styled from "styled-components";
import theme from "../styles/theme";
import ActionBar from "../ActionBar";

const MtTable = styled.div`
  border-radius: 8px;
  background-color: #ffffff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.08);
  counter-reset: rowNumber;

  
  .ant-table-default > .ant-table-content > .ant-table-body > table,
  .ant-table-middle > .ant-table-content > .ant-table-body > table,
  .ant-table-small > .ant-table-content > .ant-table-body > table, 
  .ant-table-default > .ant-table-content > .ant-table-scroll > .ant-table-header > .ant-table-fixed,
  .ant-table-default > .ant-table-content > .ant-table-scroll > .ant-table-body > .ant-table-fixed,
  .ant-table-default > .ant-table-content > .ant-table-fixed-left > .ant-table-header > .ant-table-fixed,
  .ant-table-default > .ant-table-content > .ant-table-fixed-left > .ant-table-body > .ant-table-fixed,
  .ant-table-default > .ant-table-content > .ant-table-fixed-right > .ant-table-header > .ant-table-fixed,
  .ant-table-default > .ant-table-content > .ant-table-fixed-right > .ant-table-body > .ant-table-fixed {
    padding: 0px;
    .ant-table-thead {
      & > tr {
        color: ${theme.colors.DARK_OUTER_SPACE};
        & > th {
          background-color: white;
          border-bottom: 1px solid ${theme.colors.ALTO};
          padding: ${({headingCellPadding = '16px 0px 16px 24px'}) => headingCellPadding};
          &:last-child {
            padding: 16px 24px;
          }
        }
      }
    }
    .ant-table-tbody {
      & > tr {
        color: ${theme.colors.DARK_OUTER_SPACE};
        & > td {
          background-color: white;
          border-bottom: 1px solid ${theme.colors.PEARL};
          padding: ${({contentCellPadding = '12px 0px 12px 24px'}) => contentCellPadding};
          &:last-child {
            padding: 12px 24px;
          }
        }
      }
    }

    .ant-table-row {
      counter-increment: rowNumber;
      &:hover {
        .ant-table-selection-column {
          ${props =>
            !props.showMultiSelect &&
            `&:before {
            visibility: hidden;
          }
          & > span {
            visibility: visible;
          }`};
        }
      }
    }

    .ant-table-tbody {
      .ant-table-selection-column {
        ${props =>
          !props.showMultiSelect &&
          `&:before {
          content: counter(rowNumber);
          margin-left: 5px;
          position: absolute;
          color: ${theme.colors.OUTER_SPACE};
          font-size: 12px;
        }
        & > span {
          visibility: hidden;
        }`};
      }
      & > tr {
        color: ${theme.colors.DARK_OUTER_SPACE};
        td {
          border-bottom: 1px solid ${theme.colors.PEARL};
          padding: ${({cellPadding = '12px 0px 12px 24px'}) => cellPadding};
        }
        &:last-child {
          padding: 12px 24px;
        }
        &:hover {
          & > td {
            background: ${theme.colors.PORCELAIN};
            cursor: pointer;
          }
        }
        &:active {
          & > td {
            background: ${theme.colors.TROPICAL_BLUE};
            cursor: pointer;
          }
        }
      }
    }
  }
`;


class Table extends Component {
  static propTypes = {
    children: PropTypes.node,
    actionBar: PropTypes.shape({
      countText: PropTypes.string.isRequired,
      actionItem: PropTypes.arrayOf(PropTypes.node)
    }),
    onChange: PropTypes.func,
    rowSelection: PropTypes.object,
    columns: PropTypes.array,
    size: PropTypes.string
  };
  state = {
    showActionBar: false,
    showMultiSelect: false,
  };
  // static defaultProps = {
  //   size: "small"
  // }
  onChange = (selectedRowKeys, selectedRows) => {
    let {
      actionBar,
      rowSelection: { onChange }
    } = this.props;
    this.setState(() => ({
      showActionBar: actionBar && selectedRows.length > 0,
      showMultiSelect: actionBar && selectedRows.length > 0
    }));
    onChange && onChange(selectedRowKeys, selectedRows);
  };

  render() {
    let { rowSelection, actionBar, children } = this.props;
    
    let { showActionBar, showMultiSelect } = this.state;   

    /**
     * Check if rowSelection props is been passed, If yes override the onChange property of it.
     * Also if onChange prop is passed and the rowSelection is not available, create a new rowSelection object with onChange method.
     */
    const updatedRowSelection = rowSelection
      ? { ...rowSelection, onChange: this.onChange }
      : null;

    const antProps = updatedRowSelection
      ? {
          ...this.props,
          rowSelection: updatedRowSelection
        }
      : {
          ...this.props
        };
    return (
      <MtTable showMultiSelect={showMultiSelect}>
        <AntTable {...antProps}>{children}</AntTable>
        {showActionBar && (
          <ActionBar {...actionBar}>
            {actionBar ? actionBar.actionItem : false}
          </ActionBar>
        )}
      </MtTable>
    );
  }
}
Table.Column = AntTable.Column;
export default Table;
